<template>
    <p-data-table
        :value="semenList"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="semenId"
        v-model:selection="selectedSemen"
        selectionMode="single"
        @row-select="onRowSelect"
    >
        <p-column-group type="header">
            <p-row>
                <p-column
                    headerStyle="width: 3em; max-width: 3rem"
                    style="width: 3em; max-width: 3rem"
                >
                    <template #header>
                        <p-checkbox
                            v-model="selectAll"
                            :binary="true"
                            @change="selectAllOnChange"
                        />
                    </template>
                </p-column>

                <p-column header="Semen ID" />
                <p-column header="Date" />
                <p-column header="Patient Name" />
                <p-column header="Patient Chart" />

                <p-column header="Volume" colspan="1" />
                <p-column header="Concentration" colspan="1" />
                <p-column header="Total Count" colspan="1" />
                <p-column header="Total Motility" colspan="1" />
                <p-column header="Total Motile Sperm" colspan="1" />

                <p-column header="Round Cells" colspan="1" />
                <p-column header="DNA Frag" colspan="1" />
                <p-column header="Morphology" colspan="1" />

                <p-column header="Sperm Donor" colspan="1" />

                <p-column header="Comment" />
            </p-row>
        </p-column-group>

        <p-column
            headerStyle="width: 3em; max-width: 3rem"
            style="width: 3em; max-width: 3rem"
        >
            <template #body="{data}">
                <p-checkbox
                    v-model="checkList"
                    :name="data.semenId"
                    :value="data.semenId"
                />
            </template>
        </p-column>

        <p-column field="semBarcode" />
        <p-column field="semenDate" />
        <p-column field="patientName" />
        <p-column field="patientId" />

        <p-column field="volume" />
        <p-column field="concentration" />
        <p-column field="totalCount" />
        <p-column field="motility" />
        <p-column field="totalMotility" />

        <p-column field="roundCells" />
        <p-column field="fragmentation" />
        <p-column field="morphNormal" />

        <p-column field="spermDonor">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.spermDonor"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>

        <p-column field="comment" style="min-width: 280px;" />

        <template #footer>
            <p-button
                label="Create"
                class="p-mr-2 p-button-success"
                @click="create"
                v-if="hasCreatePermission"
            />
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="edit"
                :disabled="selectedSemen == null"
                v-if="hasUpdatePermission"
            />
            <p-button
                label="Delete"
                class="p-mr-2 p-button-danger"
                @click="deleteSemenConfirm"
                :disabled="selectedSemen == null"
                v-if="hasDeletePermission"
            />
          
            <p-button
                label="PDF Reports"
                class="p-mr-2 p-button-secondary"
                @click="openPdfReportStandard"
            />
        </template>
    </p-data-table>

    <semen-create-edit-dailog :type="dialog.type" :display="dialog.display" />

    <p-dialog v-model:visible="deleteDialogvisible" modal header="Delete Semen!!" :style="{ width: '30rem' }" >
        <p>
           Are you sure you want to delete?
        </p>
        <p-button label="No" class="p-mr-2" @click="cancelDeleteSemen"/>
        <p-button label="Yes" class="p-button-danger"  @click="deleteSemen" />
    </p-dialog>

</template>

<script>
import { onMounted, provide } from "vue";
import { useDialog, useSemenSetup } from "@/composable/semen";

import SemenCreateEditDailog from "@/components/semen/create-edit-dialog.vue";
import { usePermissionCheck } from "@/composable/users";
import { ref, watch } from "vue";

export default {
    components: {
        SemenCreateEditDailog
    },
    setup() {
        const {
            store,
            toast,
            semenList,
            checkList,
            selectAll,
            selectAllOnChange,
            selectedSemen,
            onRowSelect,
            setSelectedItemNull,
            getSemenList,
            deleteSemen,
            openPdfReport,
            openPdfReportStandard,
            deleteDialogvisible
        } = useSemenSetup();

        const { dialog, create, edit, close } = useDialog();
        provide("dialog", dialog);

        const {
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission
        } = usePermissionCheck();

        // watch(cyclesSummary, (newValue) => {
        //     checkList.value = [];
        // });

        

        const cancelDeleteSemen = () => {
            deleteDialogvisible.value = false;
        }

        const deleteSemenConfirm = () => {
            deleteDialogvisible.value = true;
            // deleteCycleNo.value = null;
        };

        onMounted(() => {
            setSelectedItemNull();

            getSemenList(
                store.state.artCycle.patient.id,
                store.state.artCycle.partner.id != null
                    ? store.state.artCycle.partner.id
                    : 0
            );
        });

        return {
            deleteDialogvisible,
            deleteSemenConfirm,
            cancelDeleteSemen,
            checkList,
            selectAll,
            selectAllOnChange,
            semenList,
            selectedSemen,
            onRowSelect,
            setSelectedItemNull,
            dialog,
            create,
            edit,
            close,
            deleteSemen,
            openPdfReport,
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission,
            openPdfReportStandard
        };
    }
};
</script>

